import React from 'react';
import TableView from "../TableView";
import EditData from "../EditData";
import PieChart from "../PieChart";
import BookData from "../BookData";
import {longPromise} from "../util/PdbUtil";
import EventEmitter from "../util/EventEmitter";

class PdbViewController extends EventEmitter {
    constructor(data) {
        super();
        this.views = data.map(d => new PdbView(d));
        this.dataProvider = {};
        this.dataProviderProimse = {};
    }

    async ensureData(dataNeeded) {
        const dataPromises = dataNeeded.map(d =>
            this.getDataProvider(d)
        );
        await Promise.all(dataPromises);
    }

    registerDataProvider(dataName, dataProvider) {
        console.assert(! this.dataProvider[dataName], "registerDataProvider %s re-registered", dataName);
        console.log("registerDataProvider.%s", dataName);
        this.dataProvider[dataName] = dataProvider;
        if (this.dataProviderProimse[dataName]) {
            this.dataProviderProimse[dataName].doResolve(dataProvider);
        }
    }

    _getDataProvider(dataName) {
        if (this.dataProvider[dataName]) {
            return Promise.resolve(this.dataProvider[dataName]);
        }
        this.views.filter(v => v.providesData.includes(dataName)).map(v => v.createComponent());
        if (this.dataProvider[dataName]) {
            return Promise.resolve(this.dataProvider[dataName]);
        }
        return longPromise();
    }

    async getDataProvider(dataName) {
        if (! this.dataProviderProimse[dataName]) {
            this.dataProviderProimse[dataName] = this._getDataProvider(dataName);
        }
        return this.dataProviderProimse[dataName];
    }

    async editData(editDataProps) {
        let promise = longPromise();
        this.emit("edit", {...editDataProps, promise: promise})
        return promise;
    }
}

class PdbView extends EventEmitter {
    constructor(data) {
        super();
        this.id = data.id;
        this.name = data.name;
        this.viewBuilder = data.builder;
        this.needsData = data.needsData || [];
        this.providesData = data.providesData || [];
        this._state = 'HIDE';
    }

    async createComponent() {
        async function _createComponent() {
            console.group("createComponent:%s", this.name);
            await myPdbViewController.ensureData(this.needsData);
            const view = this.viewBuilder(this);
            console.groupEnd();
            this.state = 'SHOWING';
            return view;
        }

        if (!this.createComponentPromise) {
            this.createComponentPromise = _createComponent.apply(this);
        }
        return await this.createComponentPromise;
    }

    providesTableData(tableDataProvider) {
        console.assert(this.providesData.length === 1);
        myPdbViewController.registerDataProvider(this.providesData[0], tableDataProvider);
    }

    set state(newState) {
        console.group("setState.%s=%s (was %s)", this.name, newState, this._state);
        this._state = newState;
        this.emit('state');
        console.groupEnd();
    }

    get state() {
        return this._state;
    }
}

const myPdbViewController = new PdbViewController([
    {
        id: 1,
        name: "Table",
        builder: (pdbView) => <TableView pdbView={pdbView}/>,
        needsData: ['books'],
    },
    {
        id: 2,
        name: "Form",
        builder: (pdbView) => <EditData pdbView={pdbView}/>,
        needsData: ['books'],
    },
    {
        id: 3,
        name: "Pie Chart",
        builder: (pdbView) => <PieChart pdbView={pdbView}/>,
        needsData: ['books'],
    },
    {
        id: 4,
        name: "Book CSV",
        builder: (pdbView) => <BookData pdbView={pdbView}/>,
        providesData: ['books'],
    },
]);

export default myPdbViewController;
