import BookDataRaw from "./BookDataRaw";

import Papa from 'papaparse';
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import FilledInput from "@material-ui/core/FilledInput";
import React, {useEffect} from "react";

const parsedCsv = Papa.parse(BookDataRaw);

parsedCsv.errors.forEach(e => console.error("%s: %s (%d)", e.code, e.message, e.row));

let csvRows = parsedCsv.data;
csvRows.forEach( r => {
    r.splice(3, 1);
    r.splice(5,4);
    r.splice(2,1);
})

let firstRow = csvRows.splice(0, 1);
let theSchema = {
    type: "array",
    name: "books",
    items: {
        type: "object",
        properties: firstRow[0].reduce((map, col, colNum) => {
            let colNumAsString = String(colNum);
            map[colNumAsString] = {
                _id: colNumAsString,
                name: col,
                type: "string",
            };
            return map;
        }, {})
    },
};

csvRows = csvRows.map( (row, rowNum) => {
    return row.reduce((map, col, colNum) => {
        map[String(colNum)] = col;
        return map;
    }, {_id: String(rowNum)});
});

let callbacks = [];

const bookDataProvider = {
    getData() {
        return csvRows;
    },
    getSchema() {
        return theSchema;
    },
    registerCallback(callbackFunc) {
        callbacks.push(callbackFunc);
        return () => {
            callbacks = callbacks.filter(it => it !== callbackFunc);
        }
    },
    async updateData(key,  newData) {
        console.group("BookData.updateData");
        console.assert(key.length === 1);
        const rowNum = Number(key);
        const newRows = Array.from(csvRows);
        newRows[rowNum] = {...newRows[rowNum], ...newData};
        csvRows = newRows;
        callbacks.forEach( it => it.call());
        console.groupEnd();
        return {key};
    },
};

export default function BookData(props) {
    const {pdbView} = props;

    useEffect( () => {
        return pdbView.providesTableData(bookDataProvider);
    });

    console.log("BookData");

    return (
        <div>
            <FormControl variant="filled" fullWidth={true}>
                <InputLabel variant="filled">Columns</InputLabel>
                <FilledInput variant="filled" defaultValue={Object.keys(theSchema.items.properties).length} readOnly={true} />
            </FormControl>
            <FormControl variant="filled" fullWidth={true}>
                <InputLabel variant="filled">Row</InputLabel>
                <FilledInput variant="filled" defaultValue={csvRows.length} readOnly={true} />
            </FormControl>
            <FormControl variant="filled" fullWidth={true}>
                <InputLabel variant="filled">Length</InputLabel>
                <FilledInput variant="filled" defaultValue={BookDataRaw.length} readOnly={true} />
            </FormControl>
            <FormControl variant="filled" fullWidth={true}>
                <InputLabel variant="filled">Raw CSV</InputLabel>
                <FilledInput multiline variant="filled" defaultValue={BookDataRaw} readOnly={true}  />
            </FormControl>
        </div>
    );
}
