import React from 'react';
import {useTableData} from "./pdb/pdb-react";
import {Chart} from "react-google-charts";

export default function PieChart() {
    const {data, schema, editData} = useTableData();

    if (!data || !schema) {
        console.log("PieChart (null)");
        return null;
    }

    let firstTwoColumnsSchema = Object.entries(schema.items.properties).slice(0, 2);
    const chartData = [
        firstTwoColumnsSchema.map(([key, keySchema]) => keySchema.name),
        ...data.map(r => firstTwoColumnsSchema.map(([key, keySchema], colNum) => colNum === 0 ? r[key] : Number(r[key]))),
    ];

    console.log("PieChart", chartData);

    function editCell(rowNum, colNum) {
        editData({editKey: [rowNum], focusFieldKey: [firstTwoColumnsSchema[1][0]]}).then();
    }

    return (
        <Chart
            width={'100%'}
            height={'600px'}
            chartType="PieChart"
            loader={<div>Loading Chart...</div>}
            data={chartData}
            options={{
                title: 'Number of Books',
            }}
            rootProps={{'data-testid': '1'}}
            chartEvents={[
                {
                    eventName: 'select',
                    callback: ({chartWrapper}) => {
                        const chart = chartWrapper.getChart();
                        const selection = chart.getSelection();
                        if (selection.length === 1) {
                            const [selectedItem] = selection;
                            const {row, column} = selectedItem;
                            editCell(row, column || 1);
                        }
                    },
                },
            ]}
        />
    )
        ;
}
