import {useCallback, useEffect, useState, useRef} from "react";
import PdbViewController from '../pdbviz/PdbViewController';

export function useTableData() {
    const [state, setState] = useState({data: null, schema: null});
    const tableDataProviderRef = useRef(null);
    const dataCallbackRef = useRef(null);

    useEffect(() => {
            function updateSetState() {
                setState((oldState) => ({
                    schema: tableDataProviderRef.current.getSchema(),
                    data: tableDataProviderRef.current.getData(),
                }));
            }

            PdbViewController.getDataProvider("books").then(tableDataProvider => {
                tableDataProviderRef.current = tableDataProvider;
                updateSetState();
                dataCallbackRef.current = tableDataProvider.registerCallback(() => {
                    console.group("useTableData.callback");
                    updateSetState();
                    console.groupEnd();
                });
            });

            return () => {
                if (dataCallbackRef.current) {
                    dataCallbackRef.current.call();
                    dataCallbackRef.current = null;
                }
            };
        }
        ,
        []
    );

    const editData = useCallback((editDataProps) => {
        return PdbViewController.editData({
            ...editDataProps,
            dataProvider: "books",
        });
    }, []);

    return {data: state.data, schema: state.schema, editData};
}
