import React from 'react';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import FilledInput from '@material-ui/core/FilledInput';
import {makeStyles} from '@material-ui/core/styles';
import {useTableData} from "./pdb/pdb-react";

const useStyles = makeStyles((theme) => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            width: '25ch',
        },
    },
}));

export default function FormPropsTextFields() {
    const classes = useStyles();

    const {data, schema} = useTableData();

    if (!data || !schema) {
        console.log("EditData (null)");
        return null;
    }

    console.log("EditData");

    return (
        <div>
            {
                data.map((d, di) => (
                    <div key={`row-${d._id}`}>
                        <form key={`form-${d._id}`} className={classes.root} noValidate autoComplete="off">
                            {
                                Object.entries(schema.items.properties).map(([key, keySchema]) => {
                                    return (
                                        <div key={`dfielddiv-${d._id}-${key}`}>
                                            <FormControl key={`fielddiv-${d._id}-${key}`} variant="filled" fullWidth={true}>
                                                <InputLabel htmlFor={`field-${d._id}-${key}`}
                                                            variant="filled">{keySchema.name}</InputLabel>
                                                <FilledInput id={`field-${d._id}-${key}`} multiline variant="filled"
                                                    /*aria-describedby={`field-${di}-${ci}-helper`}*/
                                                             defaultValue={d[key]}/>
                                                {/*<FormHelperText id={`field-${di}-${ci}-helper`}>Column {di} Row {ci}</FormHelperText>*/}
                                            </FormControl>
                                        </div>
                                    );
                                })
                            }
                        </form>
                        <hr/>
                    </div>
                ))
            }
        </div>
    );
}
