import {useEffect, useState} from "react";
import PdbViewController from "./pdbviz/PdbViewController";
import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import FilledInput from "@material-ui/core/FilledInput";

function applySchemaAndDataNav(key, schema, data) {
    if (!key || key.length === 0) {
        return [schema, data];
    }

    const [thisKey, ...nextKeys] = key;
    switch (schema.type) {
        case "array":
            return applySchemaAndDataNav(nextKeys, schema.items, data[Number(thisKey)]);
        case "object":
            return applySchemaAndDataNav(nextKeys, schema.properties[thisKey], data[thisKey]);
        default:
            throw new Error(`can't understand schema ${schema.type}`);
    }
}

export function EditPopup() {
    const [editDataProps, setEditDataProps] = useState(null);
    const [openSaveWarning, setOpenSaveWarning] = useState(false);
    const fullWidth = true;
    const maxWidth = 'lg';

    useEffect(() => {
        return PdbViewController.addListener(async (evt, editDataProps) => {
            if (evt === 'edit') {
                try {
                    const dataProvider = await PdbViewController.getDataProvider(editDataProps.dataProvider);
                    const [schema, data] = applySchemaAndDataNav(editDataProps.editKey, dataProvider.getSchema(), dataProvider.getData());
                    setEditDataProps(() => ({
                        ...editDataProps,
                        dataProvider,
                        schema,
                        data,
                        oldData: data,
                        dirty: false
                    }));
                } catch (e) {
                    editDataProps.promise.doReject(e);
                }
            }
        });
    }, []);

    const closeSaveWarning = () => {
        setOpenSaveWarning(() => false);
    };

    const handleSave = async () => {
        closeSaveWarning();
        if (editDataProps.dirty) {
            console.log("saved", editDataProps.data);
            const {key} = await editDataProps.dataProvider.updateData(editDataProps.editKey, editDataProps.data);
            editDataProps.promise.doResolve({key});
            setEditDataProps(() => null);
        } else {
            handleCancel();
        }
    };

    const handleClose = () => {
        if (editDataProps.dirty) {
            setOpenSaveWarning(true);
        } else {
            handleCancel();
        }
    };

    const handleCancel = () => {
        closeSaveWarning();
        editDataProps.promise.doResolve(null);
        setEditDataProps(null);
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        handleSave().then();
    }

    const setTopLevelData = (newData) => {
        setEditDataProps((oldEditDataProps) => ({
            ...oldEditDataProps,
            dirty: true,
            data: newData,
        }));
    };

    return (
        <React.Fragment>
            {
                editDataProps === null
                    ? null
                    : <Dialog
                        fullWidth={fullWidth}
                        maxWidth={maxWidth}
                        open={editDataProps !== null}
                        onClose={handleClose}
                        aria-labelledby="edit-popup-dialog-title"
                    >
                        <DialogTitle id="edit-popup-dialog-title">Edit data</DialogTitle>
                        <DialogContent>
                            {
                                editDataProps === null
                                    ? null
                                    : <form noValidate onSubmit={handleSubmit}>
                                        <PdbEditSchemaData
                                            data={editDataProps.data}
                                            schema={editDataProps.schema}
                                            focusFieldKey={editDataProps.focusFieldKey}
                                            setData={setTopLevelData}
                                        />
                                    </form>
                            }
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleCancel} variant="contained" color="secondary">
                                Cancel
                            </Button>
                            <Button onClick={handleSave} variant="contained" color="primary">
                                Save
                            </Button>
                        </DialogActions>
                    </Dialog>
            }
            <Dialog
                open={openSaveWarning}
                onClose={closeSaveWarning}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{"Save data before closing?"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        You've made changes; do you wish to save your changes?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={closeSaveWarning} variant="contained">
                        Close
                    </Button>
                    <Button onClick={handleCancel} variant="contained" color="secondary">
                        Cancel
                    </Button>
                    <Button onClick={handleSave} variant="contained" color="primary">
                        Save
                    </Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    );
}

function PdbEditSchemaDataString(props) {
    const {idPrefix, schema, focusFieldKey, data, subSetData} = props;
    const [selectOnFocus, setSelectOnFocus] = useState(focusFieldKey);
    const onFocus = (e) => {
        if (selectOnFocus) {
            e.target.select();
            setSelectOnFocus(false);
        }
    };
    const onKeyDown = (e) => {
        if (e.key === "Enter" && !(e.shiftKey)) {
            if (!e.altKey && (e.target.value.includes("\r") || data.includes("\r"))) {
                return;
            } else {
                e.preventDefault();
                e.stopPropagation();
                e.target.form.requestSubmit();
            }
        }
    }
    return (
        <FormControl key={`${idPrefix}--fc`} variant="filled" fullWidth={true}>
            <InputLabel htmlFor={`${idPrefix}--input`} variant="filled">{schema.name}</InputLabel>
            <FilledInput id={`${idPrefix}--input`} multiline variant="filled" autoFocus={focusFieldKey}
                         defaultValue={data} onChange={subSetData} onFocus={onFocus} onKeyDown={onKeyDown}/>
        </FormControl>
    );
}

function PdbEditSchemaData(props) {
    const {data, schema, focusFieldKey, setData, idPrefix = 'edit-popup'} = props;

    switch (schema.type) {
        case "object": {
            return Object.keys(schema.properties).map((key) => {
                const [subSchema, subData] = applySchemaAndDataNav([key], schema, data);
                const subSetData = (newSubData) => {
                    const newData = {...data};
                    newData[key] = newSubData;
                    setData(newData);
                };
                return (
                    <PdbEditSchemaData
                        data={subData}
                        schema={subSchema}
                        setData={subSetData}
                        idPrefix={`idPrefix-${key}`}
                        key={`idPrefix-${key}`}
                        focusFieldKey={focusFieldKey[0] === key}
                    />
                );
            });
        }

        case "string": {
            const subSetData = (e) => {
                const newSubData = e.target.value.trim();
                if (newSubData !== data) {
                    setData(newSubData);
                }
            };
            return <PdbEditSchemaDataString
                idPrefix={idPrefix}
                schema={schema}
                focusFieldKey={focusFieldKey}
                data={data}
                subSetData={subSetData}
            />;
        }

        default:
            throw new Error(`Unknown schema type ${schema.type}`);
    }
}