import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {useTableData} from "./pdb/pdb-react";

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

export default function TableView() {
    const classes = useStyles();

    const {data, schema, editData} = useTableData();

    if (!data || !schema) {
        console.log("TableView (null)");
        return null;
    }

    console.log("TableView");

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table" stickyHeader>
                <TableHead>
                    <TableRow>
                        {
                            Object.entries(schema.items.properties).map(([key, keySchema]) => <TableCell key={`col-${key}`}>{keySchema.name}</TableCell>)
                        }
                    </TableRow>
                </TableHead>
                <TableBody>
                    {data.map((row, rowNum) => (
                        <TableRow key={`row-${rowNum}`} hover>
                            {
                                Object.entries(schema.items.properties).map(([key, keySchema]) => (
                                        <TableCell key={`cell-${rowNum}-${key}`}
                                                   onDoubleClick={() => editData({editKey: [rowNum], focusFieldKey: [key]})}>{row[key]}</TableCell>
                                ))
                            }
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
