import React, {useEffect, useState} from 'react';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import PbdTopNav from './pdbviz/PdbTopNav';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PdbViewController from './pdbviz/PdbViewController';
import {EditPopup} from "./EditPopup";

function TabPanel(props) {
    const {value, view, ...other} = props;
    const [children, setChildren] = useState(null);

    useEffect(() => {
        if (value === view.id && children == null) {
            view.createComponent();
        }
    }, [value, view, children]);

    useEffect(() => {
        return view.addListener(() => {
            if (view.state === 'SHOWING') {
                view.createComponent().then(v => {
                    setChildren(() => v);
                    view.state = 'SHOW';
                });
            }
        });
    });

    return (
        <div
            role="tabpanel"
            hidden={value !== view.id}
            id={`simple-tabpanel-${view.id}`}
            aria-labelledby={`simple-tab-${view.id}`}
            {...other}
        >
            <div>{children ? children : "Building..."}</div>
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

function App() {
    const [value, setValue] = React.useState(PdbViewController.views[0].id);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    console.log("App");

    return (
        <Container component="main" maxWidth="xl">
            <CssBaseline/>
            <PbdTopNav/>
            <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
                {
                    PdbViewController.views.map((v, vNum) => (
                        <Tab key={v.id} value={v.id} label={v.name} {...a11yProps(vNum)} />
                    ))
                }
            </Tabs>
            {
                PdbViewController.views.map(
                    (v, vNum) => (
                        <TabPanel key={v.id} value={value} view={v}/>
                    )
                )
            }
            <EditPopup/>
        </Container>
    );
}

export default App;
